<template>
  <header id="header">
    <div class="mowtainer mowtain" v-if="$route.meta.show">
      <div class="logo d-flex align-items-center">
        <router-link
          tag="img"
          to="/"
          src="img/logo.svg"
          height="45px"
          style="cursor: pointer"
        ></router-link>
      </div>
      <div class="menu d-flex">
        <a @click="scrollTo('about')">About</a>
        <a @click="scrollTo('tokenomic')">Tokenomics</a>
        <a @click="scrollTo('roadmap')">Roadmap</a>
        <a @click="scrollTo('faq')">F.A.Q</a>
        <a href="">WhitePaper</a>
      </div>
      <input type="checkbox" id="menu" style="display: none" />
      <label class="menu-bar" for="menu"></label>
      <div class="mobile-menu">
        <a @click="scrollTo('about')">
          <img src="/img/plum.svg" class="icon" />
          About
        </a>
        <a @click="scrollTo('tokenomic')">
          <img src="/img/plum.svg" class="icon" />
          Tokenomics
        </a>
        <a @click="scrollTo('roadmap')">
          <img src="/img/plum.svg" class="icon" />
          Roadmap
        </a>
        <a @click="scrollTo('faq')">
          <img src="/img/plum.svg" class="icon" />
          F.A.Q
        </a>
        <a href="">
          <img src="/img/plum.svg" class="icon" />
          WhitePaper
        </a>
      </div>
    </div>
  </header>
</template>

<script>
import { mapGetters } from "vuex";
import Web3 from "web3";
let web3 = new Web3(Web3.givenProvider);
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["account", "balance"]),
    name() {
      return this.account.slice(0, 4) + "..." + this.account.slice(-3);
    },
  },
  mounted() {
    console.log(window.web3.currentProvider.isMetaMask);
    window.onscroll = function (e) {
      let a = document.querySelector("#menu");
      if (a) a.checked = false;
    };
  },
  methods: {
    scrollTo(id) {
      document.getElementById(id).scrollIntoView(true);
      document.querySelector("#menu").checked = false;
    },
    async connectWallet() {
      if (typeof window.ethereum !== "undefined") {
        localStorage.setItem("logged", true);
        let address = await ethereum.request({ method: "eth_requestAccounts" });
        this.$store.dispatch("setAccount", address[0]);
        let balance = await web3.eth.getBalance(address[0]);
        this.$store.dispatch("setBalance", balance);
      } else {
        this.$toast.error("MetaMask is not installed");
      }
    },
    copy() {
      var copyText = document.querySelector("#address");
      copyText.select();
      copyText.setSelectionRange(0, 99999);
      document.execCommand("copy");
      this.$toast.success("Copy successful");
    },
    logout() {
      localStorage.removeItem("logged");
      this.$store.dispatch("setAccount", "");
      this.$store.dispatch("setBalance", "");
    },
  },
};
</script>
