<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
export default {
  components: { VueSlickCarousel },
  data() {
    return {
      faq: "",
      settings: {
        dots: false,
        arrows: false,
        autoplaySpeed: 2000,
        // autoplay: true,
        infinite: true,
        speed: 500,
        slidesToShow: 5,
        slidesToScroll: 1,
        initialSlide: 0,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
            },
          },
        ],
      },
    };
  },
  methods: {
    open(i) {
      if (this.faq == i) this.faq = "";
      else this.faq = i;
    },
    styleShow(i) {
      if (this.faq == i) {
        return {
          "padding-top": "15px",
          height: "auto",
          opacity: 1,
          "border-top": "1px solid #b425ff",
          "margin-top": "15px",
        };
      }
    },
    percent(value) {
      return {
        width: value + "%",
      };
    },
  },
};
