import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import AOS from 'aos'
import axios from 'axios'
import '@fortawesome/fontawesome-free/css/all.min.css'
import 'aos/dist/aos.css'
import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
import Particles from "particles.vue";

import {BootstrapVue, IconsPlugin} from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import './assets/css/style.css'
import './assets/css/responsive.css'

Vue.use(Particles);
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);

Vue.config.productionTip = false

Vue.use(Toast, {
  transition: "Vue-Toastification__bounce",
  maxToasts: 20,
  newestOnTop: true
});
Vue.prototype.logged = localStorage.getItem("token")

Vue.prototype.$urlAPI = 'http://0fd5dda96372.ngrok.io/'

Vue.prototype.$config = {
  headers: {
    "Authorization": "Bearer " + localStorage.getItem("token"),
  },
};

Vue.prototype.CallAPI = function (method, url, data, callResponse, callError) {
  switch (method) {
    case "post":
      axios.post(this.$urlAPI + url, data, this.$config).then((response) => {
        if (response.data.statusCode === 401) {
          sessionStorage.clear();
          location.reload();
        }
        callResponse(response)
      })
        .catch((error) => {
          callError(error)
        })
      break;
    case "get":
      axios.get(this.$urlAPI + url, this.$config)
        .then((response) => {
          if (response.data.statusCode === 401) {
            sessionStorage.clear();
            location.reload();
          }
          callResponse(response)
        })
        .catch((error) => {
          callError(error)
        })
      break;
    case "put":
      axios.put(this.$urlAPI + url, data, this.$config)
        .then((response) => {
          if (response.data.statusCode === 401) {
            sessionStorage.clear();
            location.reload();
          }
          callResponse(response)
        })
        .catch((error) => {
          callError(error)
        })
      break;
    case "delete":
      axios.delete(this.$urlAPI + url, this.$config)
        .then((response) => {
          if (response.data.statusCode === 401) {
            sessionStorage.clear();
            location.reload();
          }
          callResponse(response)
        })
        .catch((error) => {
          callError(error)
        })
      break;
  }
}

new Vue({
  created() {
    AOS.init()
  },
  router,
  store,
  axios,
  render: h => h(App)
}).$mount('#app')
